import React, { Component } from 'react';
import { getAllCoin } from '../getCoin';
import Moeda from '../components/Moeda';
import '../css/Home.css'

export default class Home extends Component {
  constructor(){
    super()
    this.state = {
      moedas: [],
    }
  };

  componentDidMount(){
    this.gerarMoedas();
  }

  gerarMoedas = async () => {
    getAllCoin().then((data) => {
      const {moedas} = this.state;
      this.setState({moedas: moedas.concat(data.data)});
    }).catch((error) => this.gerarMoedas());
  }

  render(){
    const {moedas} = this.state;
    return(
      <section>
        <h1>CRIPTOMOEDAS</h1>
        <div className='tbl-header'>
          <table cellPadding='0' cellSpacing='0' border='0'>
            <thead>
              <tr>
                <th>SYMBOL</th>  
                <th>RANK</th>
                <th>NAME</th>
                <th>USD</th>
                <th>BRL</th>
              </tr>  
            </thead>
          </table>
        </div>

        <div className='tbl-content'>
          <table cellPadding='0' cellSpacing='0' border='0'>
            <tbody>
              { moedas.map((data , index) => <Moeda key={index} mode={data}/>)}
            </tbody>
          </table>
        </div>
      </section>
    );
  }
}