import React, { Component } from 'react';
import { converterMoedas } from '../convertCoin';

export default class Moeda extends Component {
  constructor(){
    super()
    this.state = {
      moeda: 0,
    }
  }
  componentDidMount(){
    converterMoedas().then((data) => {
      const result = data.USDBRL.high * this.props.mode.priceUsd
      this.setState({moeda: result});
    });
  }

  render() {
    const { mode: { symbol, rank, name, priceUsd }} = this.props;
    const { moeda } = this.state;
    return(
      <tr className='itens'>
        <th>{ symbol }</th>
        <th>{ rank }</th>
        <th>{ name }</th>
        <th>{ `$${parseFloat(priceUsd).toFixed(2).replace('.',',')}` }</th>
        <th>{ `R$${parseFloat(moeda).toFixed(2).replace('.',',')}` }</th>
    </tr>
    );
  }
}
